/* Default styles for navigation bar */
.navbar {
  /* Your default styles here */
}

/* Styles for smaller screens (up to 600px) */
@media screen and (max-width: 600px) {
  .navbar {
    flex-direction: column; /* Stack items vertically */
    align-items: center; /* Center items */
  }

  .navbar ul {
    width: 100%; /* Full width */
    text-align: center; /* Center text */
  }

  .navbar li {
    margin: 10px 0; /* Add vertical margin between items */
  }
}

/* Styles for screens between 601px and 992px */
@media screen and (min-width: 601px) and (max-width: 992px) {
  .navbar li {
    margin: 0 10px; /* Add horizontal margin between items */
  }
}

/* Styles for larger screens (993px and above) */
@media screen and (min-width: 993px) {
  .navbar {
    /* Additional styles for larger screens if needed */
  }
}
